<template>
  <div id="app">
    <router-view/>
  </div>
</template>

<script>
  export default {
    name: 'App',
    created() {
      this.IsPC();
//      this.autoLogin();
//    this.checkLogin();
    },
    methods: {
      /*
      * 自动登录
      * */
      autoLogin(){
        this.$request.get('Login/autoLogin','',res=>{
          if(res.code == 200){
            this.setCookie('username', res.data.userId, 1);
          }else{
            alert(res.msg);
          }
        })
      },
      /*
      * 检测是否登录
      * reture:[userId, alias, head_add, groupId]
                groupId=0是微信账号登录，1-5是临时登录
      * */
      checkLogin() {
        this.$request.get('/Login/checkLogin', '', res => {
          if (res.code == 200) {
            //已登录
            if(res.data.groupId != 0){//临时登录
              this.setCookie('username', res.data.userId, 1);
            }else if(res.data.groupId == 0 ){//微信登录
              this.setCookie('username', res.data.userId, 1);
              this.setCookie('iswxLogin', 1, 1);
            }
          } else if (res.code == -5002) {
            //未登录
            this.autoLogin();
          }
        })
      },
      IsPC() {
        var href = window.location.href;
        var ua = navigator.userAgent;
        var ipad = ua.match(/(iPad).*OS\s([\d_]+)/),
          isIphone = !ipad && ua.match(/(iPhone\sOS)\s([\d_]+)/),
          isAndroid = ua.match(/(Android)\s+([\d.]+)/),
          isMobile = isIphone || isAndroid || ipad;

        if (isMobile) {

          if (href.indexOf('H5') < 0) {
            window.location.href = 'http://' + document.location.hostname + '/H5.html'
          }

        } else {

          if (href.indexOf('H5') > 0) {
            window.location.href = 'http://' + document.location.hostname
          }


        }

      }
    }
  }
</script>

<style>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    /* text-align: center; */
    color: #2c3e50;
    height: 100%;
    /* margin-top: 60px; */
  }
</style>



