<template>
  <main class="tab-container">
    <nav class="clearfix">
      <div @click="selected = 0" :class="{'active':selected == 0}">课程表</div>
      <div @click="selected = 1" :class="{'active':selected == 1}">免责声明</div>
    </nav>
    <!--课程表-->
    <section class="content" v-if="selected == 0">
      <img :src="courseBg" alt="">
    </section>
    <!--免责声明-->
    <section class="content" v-if="selected == 1">
      <img :src="disclaimer" alt="">
    </section>
  </main>
</template>
<script>

  export default {
    props: ['courseBg','disclaimer'],
    data(){
      return{
        selected:0
      }
    }
  }
</script>
<style lang="scss">
  .tab-container{
    border: 1px solid #eeeeee;
    width:100%;
    background: #fff;
    nav{
      font-size: 18px;
      color: #353535;
      border-bottom: 1px solid #eeeeee;
      font-weight: bold;
      div{
        height: 50px;
        line-height: 50px;
        float: left;
        padding:0 16px;
        cursor: pointer;
        border-top: 4px solid #fff;
        box-sizing: border-box;
        border-left: 1px solid #fff;
        border-right: 1px solid #fff;
        &.active{
          color: #ff5e2d;
          border-top: 4px solid #ff5e2d;
          border-left: 1px solid #eeeeee;
          border-right: 1px solid #eeeeee;
          position: relative;
          &::after{
            content: '';
            display: inline-block;
            position: absolute;
            bottom:-1px;
            left: 0;
            width: 100%;
            height: 2px;
            background: #fff;
          }
        }
      }
    }
    .content{
      padding: 16px;
      img{
        width: 100%;
      }
    }
  }
</style>



