<template>
  <main class="login">
    <section class="logo">
      <div class="center">
        <img src="../assets/images/jiahua_logo.png" alt="">
      </div>
    </section>
    <section class="main">
      <section class="login-box">
        <h1>用户登录</h1>
        <section class="content">
          <div class="block">
            <div class="title">
              <span>用户名</span>
              <i v-show="errLoginName">用户名不存在</i>
            </div>
            <div class="inp">
              <input type="text" placeholder="输入用户名" v-model="loginName">
            </div>
          </div>
          <div class="block">
            <div class="title">
              <span>密码</span>
              <i v-show="errPassword">密码错误</i>
            </div>
            <div class="inp">
              <input type="password" placeholder="输入密码" v-model="password">
            </div>
          </div>
          <div class="block">
            <div class="title">
              <span>图形验证</span>
              <i v-show="errAuthCode">验证码错误</i>
            </div>
            <div class="inp auth-code">
              <input type="number" maxlength="4" placeholder="输入图形中的数字" v-model="inpCode" @keyup.enter="loginFn">
              <div class="pic">
                <canvas id="canvas" width="100%" height="50px" @click="drawPic"></canvas>
              </div>
            </div>
            <p class="err-msg" v-show="errMsg">{{errMsg}}</p>
          </div>
          <div class="login-btn" @click="loginFn">立即登录</div>
        </section>
      </section>
    </section>
  </main>
</template>
<script>
  export default {
    data() {
      return {
        loginName: '',
        password: '',
        inpCode:'',//用户输入的验证码
        authCode:'', //随机图形验证码
        errLoginName:false, //用户名不存在
        errPassword:false, //密码错误
        errAuthCode:false, //验证码错误
        errMsg:''//错误信息
      }
    },
    created() {
    },
    mounted(){
      this.drawPic();
    },
    methods: {
      /**
       * 登录函数
       * loginName：用户名
       * password：密码
       */
      loginFn() {
        console.log('验证码',this.authCode);
        if(this.inpCode != this.authCode){
          this.errAuthCode = true;
          return;
        }
        this.$request.post("Login/login", {
          loginName: this.loginName,
          password: this.password
        }, res => {
          if(res.code == 200){
            //登录成功
            this.setCookie('username', this.loginName, 1)
            this.$router.push('/');
          }else if(res.code == -16004){
            //用户名不存在
            this.errLoginName = true;
          }else if(res.code == -16008){
            //密码错误
            this.errPassword = true;
          }else{
            this.errMsg = res.msg;
          }
        })
      },
      /**
       * 生成一个随机数
       */
      randomNum(min, max) {
        return Math.floor(Math.random() * (max - min) + min);
      },
      /*
      * 生成一个随机色
      * */
      randomColor(min, max) {
        var r = this.randomNum(min, max);
        var g = this.randomNum(min, max);
        var b = this.randomNum(min, max);
        return `rgb(${r},${g},${b})`;
      },
      /*
      *绘制验证码图片
      * */
      drawPic() {
        this.authCode = '';
        this.errAuthCode = false;
        var canvas = document.getElementById("canvas");
        var width = canvas.width;
        var height = canvas.height;
        var ctx = canvas.getContext('2d');
        ctx.textBaseline = 'bottom';
        /**绘制背景色**/
        ctx.fillStyle = this.randomColor(180, 230); //颜色若太深可能导致看不清
        ctx.fillRect(0, 0, width, height);
        /**绘制文字**/
        var str = '0123456789';
        for (var i = 0; i < 4; i++) {
          var txt = str[this.randomNum(0, str.length)];//随机的字
          ctx.fillStyle = this.randomColor(50, 160);  //随机生成字体颜色
          ctx.font = this.randomNum(18, 40) + 'px SimHei'; //随机生成字体大小
          var x = 10 + i * 25;
          var y = this.randomNum(25, 45);
          var deg = this.randomNum(-30, 30);//字体的旋转角度
          //修改坐标原点和旋转角度
          ctx.translate(x, y);
          ctx.rotate(deg * Math.PI / 180);
          ctx.fillText(txt, 0, 0);
          //恢复坐标原点和旋转角度
          ctx.rotate(-deg * Math.PI / 180);
          ctx.translate(-x, -y);
          this.authCode += txt;
        }
        /**绘制干扰线**/
        for (var i = 0; i < 5; i++) {
          ctx.strokeStyle = this.randomColor(180,230);
          ctx.beginPath();
          ctx.moveTo(this.randomNum(0, width), this.randomNum(0, height));
          ctx.lineTo(this.randomNum(0, width), this.randomNum(0, height));
          ctx.stroke();
        }
        /**绘制干扰点**/
        for (var i = 0; i < 40; i++) {
          ctx.fillStyle = this.randomColor(150,200);
          ctx.beginPath();
          ctx.arc(this.randomNum(0, width), this.randomNum(0, height), 1, 0, 2 * Math.PI);
          ctx.fill();
        }
      }
    },
    watch:{
      loginName(){
        this.errLoginName = false;
        this.errMsg = '';
      },
      password(){
        this.errPassword = false;
        this.errMsg = '';
      },
      inpCode(){
        this.errAuthCode = false;
      }
    }
  }
</script>
<style lang="scss">
  $mainColor: #ff5e2d;
  input[type=number] {
    -moz-appearance: textfield;
  }

  input[type=number]::-webkit-inner-spin-button,
  input[type=number]::-webkit-outer-spin-button {
    -webkit-appearance: none;
    margin: 0;
  }

  .login {
    width: 100%;
    height: 100%;
    background: #f6f6f6;
    .logo {
      background: #fff;
      height: 50px;
      border-bottom: 1px solid #eeeeee;
      box-sizing: border-box;
      .center {
        width: 1200px;
        margin: 0 auto;
        img {
          width: 150px;
          margin-top: 11px;
        }
      }
    }
    .main {
      width: 1200px;
      height: calc(100% - 50px);
      margin: 0 auto;
      background: url("../assets/images/register_bg.png") no-repeat;
      -webkit-background-size: 100% 100%;
      background-size: 100% 100%;
      position: relative;

    }
    .login-box {
      width: 400px;
      border: 1px solid #eeeeee;
      position: absolute;
      top: 64px;
      right: 40px;
      background: #fff;
      h1 {
        line-height: 64px;
        height: 64px;
        padding-left: 32px;
        font-size: 22px;
        border-bottom: 1px solid #eeeeee;
        color: $mainColor;
        font-weight: bold;
      }
      .content {
        padding: 48px 32px;
        .block {
          margin-bottom: 24px;
        }
        .title {
          font-size: 14px;
          margin-bottom: 16px;
          span {
            color: #353535;
          }
          i {
            float: right;
            color: $mainColor;
            font-style: normal;
          }
        }
        .inp {
          height: 50px;
          border: 1px solid #f2f2f2;
          input {
            font-size: 18px;
            color: #353535;
            padding-left: 16px;
            width: 100%;
            height: 100%;
          }
          &.auth-code {
            display: flex;
            input {
              width: 240px;
              font-size: 18px;
              color: #353535;
              padding-left: 16px;
            }
            .pic {
              flex: 1;
              background: #eeeeee;
            }
            #canvas{
              cursor: pointer;
            }
          }
        }
        .login-btn {
          margin-top: 68px;
          width: 100%;
          height: 50px;
          line-height: 50px;
          font-size: 20px;
          color: #fff;
          background: $mainColor;
          text-align: center;
          cursor: pointer;
        }
        .err-msg{
          color: $mainColor;
          font-size: 14px;
          margin-top: 10px;
        }
      }
    }
  }
</style>



