<template>
  <main class="on-line">
    <header>
      <p>在线人数({{visitCount}})</p>
    </header>
    <ul id="ul">
      <li v-for="item in visitList">
        <img class="head" :src="item.chead" alt="">
        <span>{{item.useralias}}</span>
        <img v-if="item.userlevel == 2" class="logo" src="../assets/images/1.1/teacher_label.png" alt=""><!--老师-->
        <img v-if="item.userlevel == 3" class="logo" src="../assets/images/1.1/assistant_label.png" alt=""><!--助理-->
      </li>

    </ul>
  </main>
</template>
<script>
  export default {
    props:['visitCount','visitList']
  }
</script>
<style lang="scss">
  .on-line{
    background: #222;
    color: #888;
    height: 100%;
    overflow: hidden;
    box-sizing: border-box;
    header{
      padding-left: 16px;
      line-height: 72px;
      height: 72px;
      background: #2a2a2a;
      font-size: 18px;
      color: #fff;
    }
    ul{
      padding:8px 0;
      overflow-y: scroll;
      height: calc(100% - 72px);
      height: -moz-calc(100% - 72px);
      height: -webkit-calc(100% - 72px);
      background: #222222;
      li{
        padding:8px 16px;
        img.head{
          width: 30px;
          height: 30px;
          border-radius: 50%;
          margin-right: 8px;
          vertical-align: middle;
        }
        .logo{
          width: 36px;
          float: right;
          border-radius: 0;
          height: 19px;
          margin-top: 4px;
        }
      }
    }
     /*滚动条样式*/
    #ul::-webkit-scrollbar,
    .on-line::-webkit-scrollbar{
        width: 6px;
        height: 8px;
        background-color: #1a1a1a;
    }
    /*定义滚动条的轨道，内阴影及圆角*/
    #ul::-webkit-scrollbar-track,
    .on-line::-webkit-scrollbar-track{
        -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
        border-radius: 3px;
        background-color: #4b4b4b;
    }

    /*定义滑块，内阴影及圆角*/
    #ul::-webkit-scrollbar-thumb,
    .on-line::-webkit-scrollbar-thumb{
        height: 10px;
        border-radius: 3px;
        -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
        background-color: #1a1a1a;
    }
  }
</style>



