<template>
    <div class="emoji"  v-show="isEmojiBox" >

        <div class="reward active"  >
            <div class="emoji-container">
                <swiper :options="emSwiperOption" ref="mySwiperq" >
                   <swiper-slide v-for="item in isEmojiData" :key="item.id">
                        <a href="javascript:;"  @click="emojiSave(item.expression_number)" >
                            <div class="gift-img">
                                <img :src="item.qiniuImg" >
                            </div>
                            <h4>{{item.name}}</h4>
                        </a>
                    </swiper-slide>
                    <div class="swiper-pagination" slot="pagination" ></div>
                </swiper>
            </div>
        </div>

    </div>
</template>
<script>
    import {swiper, swiperSlide} from "vue-awesome-swiper";
    require("swiper/dist/css/swiper.min.css");
    import wxfun from "../assets/js/live/wxfunction.js";
    export default {
        props: ['isEmojiBox','userId','courseId','type'],
        data() {
            return {
                isEmojiData:[],//表情數據
                expressionNumber:[],//表情码
                emSwiperOption: {
                  // direction: 'vertical',
                    slidesPerGroup: 4,
                    loopFillGroupWithBlank: true,
                    slidesPerView : 4,
                    slidesPerColumn:2,
                    slidesPerColumnFill : 'row',
                    notNextTick: true,

                    mousewheel: true,
                    pagination: {
                        el: '.swiper-pagination',
                        clickable: true

                    },

                },
            }
        },
        computed:{
            swiper() {
                return this.$refs.mySwiperq.swiper
            },
        },
        created(){
            this.getEmoji();
        },
        methods: {
            getEmoji(){
                this.$request.get('/Expression/getExpressionList','',res => {
                    if(res.code == 200){
                        this.isEmojiData =res.data;
                        this.isEmojiData.forEach(element => {
                           this.expressionNumber.push(element.expression_number);
                        });
                    }
                });
            },
            emojiSave(number){

                    wxfun.sendMessage(
                        +this.userId,
                        +this.courseId,
                        number,
                        0,
                        0
                    );


                    setTimeout(function () {

                    },2000);



            }
        },
        components: {
            swiper,
            swiperSlide
        }
    }
</script>
<style type="text/scss" lang="scss">
    $mainColor: #c62f2f;
    $borderc: #eee;
    .emoji {
        position: absolute;
        border-top: 1px solid $borderc;
        bottom:56px;
        div, ul {
            max-width: initial;
        }
        width: 100%;
        overflow: hidden;
        .emoji-container {
            padding-top: 8px;
            height: 100%;
            .swiper-container{
                height: 240px;
                padding-left: 12px;
                .swiper-wrapper{
                    position: initial;
                    .swiper-slide{
                        height: 116px;
                        padding: 16px 0 11px;
                        box-sizing: border-box;
                        a {
                            padding-right:15%;
                            display: block;
                            width: 85%;
                            height: 116px;
                            .gift-img {
                                height: 54px;
                                img{
                                    width: 100%;
                                    height: 100%;
                                }
                            }
                            h4 {
                                padding-top: 8px;
                                font-size: 12px;
                                color: #818181;
                                display: block;
                                text-align: center;
                                overflow: hidden;
                                white-space: nowrap;
                                text-overflow: ellipsis
                            }
                        }
                    }
                }
                .swiper-pagination-bullet{
                    width: 4px;
                    height: 4px;
                    background: #888888;
                    opacity:.5;
                    margin: 0 3px;
                    border-radius: 3px;
                }
                .swiper-pagination-bullet-active{
                    background: #888888;
                    width: 7px;
                    border-radius: 3px;
                    opacity:1;
                }
                .swiper-pagination-bullets{
                    bottom:5px;
                }
            }
            &:after {
                content: "";
                display: block;
                clear: both;
            }
        }
        .reward {
            position: relative;
            width: 100%;
            transform: translateY(100%);
            transition: transform 0.3s ease;
            background: #ffffff;
            z-index: 1000;
            &.active {
                transform: translateY(0);
            }
            article{
                position: relative;
            }
            h5 {
                line-height: 50px;
                color: #1c0808;
                font-size: 16px;
                padding-left: 12px;
                height: 50px;
                border-bottom: 1px solid #e8e8e8;
                text-align: center;
                position: relative;
                span{
                    position: absolute;
                    top: 0;
                    right: 0;
                    width: 50px;
                    height: 50px;
                    // background: url("/images/cha.png") no-repeat center center;
                    background-size: 32% 32%;
                }
            }
            .reward-bottom {
                a{
                    display: block;
                }
                a:first-child {
                    font-size: 12px;
                    color: #888;
                    text-align: center;
                    padding-bottom:8px;
                    padding-top: 20px;
                }
                a:last-child {
                    display: block;
                    background-color:#888;
                    color: #fff;
                    font-size: 16px;
                    line-height: 50px;
                    height: 50px;
                    width: 100%;
                    text-align: center;
                    &.active {
                        background-color: $mainColor;
                    }
                }
            }
        }

    }
</style>



