<template>
<main class="index">
  <header>
    <section class="left">
      <img class="logo" src="../assets/images/jiahua_logo.png" alt="">
      <section class="teacher">
        <img class="head" :src="lecturer.head_add" alt="">
        <span>{{lecturer.alias}}</span>
        <img class="live-logo" src="../assets/images/1.1/name_live.gif" alt="">
      </section>
    </section>
    <section class="right">
      <!--未登录-->
      <section class="no-login" v-if="!isWxLogin">
        <img src="../assets/images/wechat_icon.png" alt="">
        <span @click="WXLoginFn">点击登录</span>
      </section>
      <!--已登录-->
      <section class="no-login" v-if="isWxLogin"><!--isWxLogin-->
        <img :src="userInfo.head_add" alt="">
        <span>{{userInfo.alias}}</span>
        <i @click="exitWXLogin">退出登录</i>
      </section>
    </section>
  </header>
  <section class="article">
      <!-- 左边在线人数 -->
      <div class="online-num">
         <onLineList :visitCount="visitCount" :visitList="visitList"></onLineList>
      </div>
      <!-- 中间直播部分 -->

      <div class="video-play">
        <!-- 直播区域 -->
        <section>
            <div  id="videoplayer" ref="videoplayer" style="height: 679px;" v-show="push_status == 1"> </div>
            <img :src="live_cover" style="height: 678px;" v-show="push_status == 2">
        </section>
        <tabber :courseBg="courseBg" :disclaimer="disclaimer"></tabber>
      </div>

      <!-- 右边评论部分 -->

      <div class="comment-box">
          <h5 class="title">
            互动区
          </h5>
          <div class="comment " id="sidebar">
            <div class="comment-item" v-for="item in comments" >
              <img :src="item.avatar" alt="">
              <div>
                <h5><span class="name">{{item.name}}</span><span class="type ass" v-if="item.type==3"></span><span class="type tea" v-if="item.type==2"></span><span class="time">{{item.time}}</span></h5>
                <p>
                  <template v-if="item.emojitype == 1">
                      <img :src="item.emojiexpression" alt="">
                  </template>
                  <template v-else>
                      {{item.content}}
                  </template>

                  </p>
              </div>
            </div>
            <span class="txt"></span>
          </div>
           <emoji :isEmojiBox="isEmojiBox" :userId="userId" :courseId="courseId"
                                               :type="1" ></emoji>
           <div class="footer">
             <template v-if="isWxLogin">
                  <input type="text" placeholder="快来和老师聊聊吧~" v-model="commentText" @keyup.enter="sendComment">
             <span class="emojiBtn" @click="toggleEmoji" :class="{active:isEmojiBox==true}"></span>
             <span id="sendBtn" @click="sendComment"></span>
             </template>

             <template v-else>
                <div class="mask-login">
                <p>登陆后才能发言哦</p>
                <span class="weixin"  @click="WXLoginFn"></span>
             </div>
             </template>

          </div>
          <!-- 历史消息btn -->
          <p class="his" v-show="commentTop">
            向上滚动加载更多历史消息
          </p>
          <!-- 评论错误通知 -->
          <p class="comment-err" v-show="commentErr">
              评论为空哦~
          </p>
      </div>
  </section>
</main>
</template>
<script>
require("../assets/js/live/web_socket.js");
import socketClient from "../assets/js/live/socketClient.js";
import wxfun from "../assets/js/live/wxfunction.js";
import tabber from "../components/tabber.vue"; //互动课堂
import emoji from "../components/emoji.vue"; //互动课堂
import onLineList from "../components/onLineList.vue"; //在线人数
export default {
  data() {
    return {
      userId: "",//用户id
      courseId: 1000010029, //当前房间id
      isEmojiBox: false, //是否显示表情
      EmojiData:[],//表情包数据
      commentText: "", //留言内容
      courseBg: "", //课程表
      disclaimer: "", //免责声明
      push_status: 2, //推流专题1正则推流，2没有推流
      comments: [], //互动区消息
      live_cover: "", //直播间封面
      pull_url: "", //直播地址
      commentLastId: 0, //最新一个评论id
      maxL: "",
      minL: "",//上一次加载最后一个msgid
      commentLoading: true, //互动区是否能向上滚动
      commentbottom: true, //3017第一次加载可以滚动到最新，后则不行
      visitCount:'', //在线人数
      visitList:[], //在线人列表
      commentbottom: true, //3017第一次加载可以滚动到最新，后则不行
      commentTop:false, // 准备加载
      commentbottom: true, //3017第一次加载可以滚动到最新，后则不行
      commentErr:false,
      metaInfo: {
        //seo
        title: "家华直播间",
        name: "家华直播间",
        content: ""
      },
      lecturer:'', //讲师信息
      userInfo:'', //微信登录用户资料,
      isWxLogin:false, //是否微信登录
      teacArr:[],//讲师、助教数组，用于计算讲师助教数量
    };
  },
  name: "async",
  metaInfo() {
    return {
      title: this.metaInfo.title,
      meta: [
        {
          name: this.metaInfo.name, //keyWords
          content: this.metaInfo.content
        }
      ],
      link: [
        {
          // set link
          rel: "",
          href: "document.location.hostname"
        }
      ]
    };
  },
  created() {
    this.liveLecturer();
    this.checkLogin();
  },
  computed:{
  },
  beforeDestroy() {
    wxfun.leaveRoom(+this.userId, +this.courseId);
    clearInterval(socketClient.setping);
    socketClient.ws.close();
  },
  mounted() {
    this.$nextTick(() => {
      document.getElementById("sidebar").addEventListener("scroll", e => {
        if (this.commentLoading == true) {
           if (document.getElementsByClassName("comment")[0].scrollTop < 200) {
              this.commentTop = true;
          }
          if (document.getElementsByClassName("comment")[0].scrollTop == 0) {
            this.commentbottom = false;
            this.getHistory(this.minL, false, 20, true);//向上滚动加载更多
            this.commentTop = false;
          }
        }
      });
    });
  },
  watch:{
  },
  components: {
    tabber,
    onLineList,
    emoji
  },
  methods: {
    /*
   * 检测是否登录
   * reture:[userId, alias, head_add, groupId]
             groupId=0是微信账号登录，1-5是临时登录
   * */
    checkLogin() {
      this.$request.get('/Login/checkLogin', '', res => {
        if (res.code == 200) {
          //已登录
          if(res.data.groupId != 0){//临时登录
            this.setCookie('username', res.data.userId, 1);
            this.isWxLogin = false;
          }else if(res.data.groupId == 0 ){//微信登录
            this.setCookie('username', res.data.userId, 1);
            this.userInfo = res.data;
            this.isWxLogin = true;
          }
        } else if (res.code == -5002) {
          //未登录
          this.autoLogin();
          this.isWxLogin = false;
        }
        this.$nextTick(()=>{
             this.getData();
        })

      })
    },
    /*
    * 微信登录
    * */
    WXLoginFn(){
      var link = window.location.href;
      this.$request.get('Login/getWXLoginUrl',{loginRedirectUrl: link},res=>{
        if(res.code == 200){//已经微信登录

        }else if(res.code == -5002){//未登录，跳转到扫码登录
          window.location.href = res.data.url;
        }
      })
    },
    /*
    * 退出微信登录
    * */
    exitWXLogin(){
      this.$request.get('Login/exitWXLogin','',res=>{
        if(res.code == 200){
          this.setCookie('username', res.data.userId, 1);
          this.isWxLogin = false;
          this.userInfo = '';
          setTimeout(()=>{
            window.location.reload();
          },100)
        }else{
          console.log('Login/exitWXLogin',res.msg);
        }
      })
    },
    /*
     * 自动登录
     * */
    autoLogin(){
      this.$request.get('Login/autoLogin','',res=>{
        if(res.code == 200){
          this.setCookie('username', res.data.userId, 1);
        }else{
          console.log('Login/autoLogin',res.msg);
        }
      })
    },
    /*
    * Lecturer/liveLecturer
    * 获取正在直播的讲师
    * */
    liveLecturer(){
      this.$request.get('/Lecturer/liveLecturer','',res=>{
        this.lecturer = res.data;
      })
    },

     /*
    * 表情包弹窗
    * */
    toggleEmoji(){
        this.isEmojiBox = this.isEmojiBox == false? true:false;
    },

    /*
    * 获取表情包
    * */
    getEmoji() {
     this.$request.get('/Expression/getExpressionList','',res => {
                    if(res.code == 200){
                        this.EmojiData =res.data;
                        console.log(res.data)
                         this.getHistory(0, false, 20, true);
                    }
                });


    },
    /*
    * 获取在线用户列表
    * */
    getOnlineList() {
      wxfun.getOnLineList(+this.userId, +this.courseId);
    },
    /*
    * 获取历史消息
    * */
    getHistory(msgId, isReadAll, count, isForward) {
      wxfun.getMsgHis(
        +this.userId,
        +this.courseId,
        msgId,
        isForward,//顺序还是倒叙
        isReadAll,//分页还是不分页
        count //条数
      );
    },
    /**
     发送评论
      * */
    sendComment(e) {
      // e.target.previousElementSibling.blur();
      if (this.commentText.trim() == "" || this.commentText.length == "") {
        this.commentErr = true;
         setTimeout(()=>{
           this.commentErr = false;
         },1000)
      } else {
        //发表评论
        if (socketClient.ws.readyState == 3) {
           alert('网络超时，请刷新')
           return false;
        }
        this.commentErr = false;
        wxfun.sendMessage(
          this.userId,
          this.courseId,
          this.commentText.replace(/[\r\n]/g, "")
        );
      }

      this.commentText = "";
    },
    /**
     时间日期格式
      * */
    formatDate(obj) {
      function f(num) {
        return num > 9 ? num + "" : "0" + num;
      }

      return (
        f(obj.getMonth() + 1) +
        "-" +
        f(obj.getDate()) +
        " " +
        f(obj.getHours()) +
        ":" +
        f(obj.getMinutes())
      );
    },
    /**
     websocket
      数据
      * */
    onmessage(msg) {
      const data = msg.data;
      console.log(msg);
      // alert(msg.subcmd);
      switch (msg.subcmd) {
        // case 2001://登录房间失败
        //     if (data.errInfo.errID == 1025) {
        //         wxfun.leaveRoom(+this.userId, +this.$route.params.courseId)
        //         this.loginSuccess = false
        //     }
        //     break
        case 2002: //登录房间成功
          console.log(data);
          // alert("登录房间成功");
          window.onbeforeunload = () => {
            wxfun.leaveRoom(+this.userId, +this.courseId);
          };
          this.getEmoji();


          this.getOnlineList();
          break;
        case 2006://在线用户列表
          this.visitList = this.visitList.concat(data.UserInfo);
          for(var i=0;i<data.UserInfo.length;i++){
            if(data.UserInfo[i].userlevel==2 || data.UserInfo[i].userlevel==3){ //讲师或助教，推进数组
              this.teacArr.push(data.UserInfo[i]);
            }
          }
          break;
        case 2008://用户进入
          var id = data.userid;
          var tag = false;
          var username = this.getCookie('username');
          if(id != username){//登录用户不是新加入的用户时
            if(data.userlevel==2 ||data.userlevel==3){//讲师或助教，添加在最前面
              this.visitList.unshift(data);
            }else{//在讲师、助教后面插入
              this.visitList.splice(this.teacArr.length,0,data);
            }
          }

          console.log('2008用户进入visitList======',this.visitList);
          break;
        case 2012: //用户退出通知
          var id = data.userid;
          //删除在线人数数组
          for(var i=0;i<this.visitList.length;i++){
            if(this.visitList[i].userid == id){
              this.visitList.splice(i,1);//删除
              break;
            }
          }
          //删除讲师、助教数组
          if(data.userlevel==2 || data.userlevel==3){
            for(var j=0;j<this.teacArr.length;j++){
              if(this.teacArr[j].userid == id){
                this.teacArr.splice(j,1);//删除
                break;
              }
            }
          }

          break;
        case 2181: //进入房间人数统计
          this.visitCount = data.visitCount;
          break;
        case 2226: //直播通知
          this.push_status = 1;
          break;
        case 2227: //直播结束通知
          this.push_status = 2;
          break;
        case 2218: //结束直播成功
          break;
        case 3012:
         if (data.msg.content.indexOf('[-') >= 0) {
            this.isEmojiBox = false;

              var emojitype = 1;
               for (var j = 0; j < this.EmojiData.length; j++) {
                  if (data.msg.content.indexOf(this.EmojiData[j].expression_number) >= 0) {
                    var emojiexpression = this.EmojiData[j].qiniuImg;
                    console.log(emojiexpression)
                  }
                }
            }
          this.comments.push({
            name: data.msg.srcUser.alias,
            avatar: data.msg.srcUser.head, // || "/images/default/userDefault.png",
            content: data.msg.content,
            type:data.msg.srcUser.UserType,//1：学生;2:老师;3：助教
            time: this.formatDate(new Date(data.msg.msgTime * 1000)),
            emojitype: emojitype || 0,
            emojiexpression: emojiexpression || 0
          });
          setTimeout(() => {//滚动到底部
            document.getElementsByClassName(
              "comment"
            )[0].scrollTop = document.getElementsByClassName(
              "txt"
            )[0].offsetTop;
          }, 500);
          break;
        case 3017: //获取聊天室消息成功
          if (data.msgs) {
            this.maxL = data.msgs[0].msgId;
            this.minL = data.msgs[data.msgs.length - 1].msgId;
            if (data.msgs.length < 20) {
              this.commentLoading = false;
              this.commentbottom = false;
            }

            for (var i = 0, len = data.msgs.length; i < len; i++) {
               if (data.msgs[i].content.indexOf('[-') >= 0) {
                    var emojitype = 1;
                    for (var j = 0; j < this.EmojiData.length; j++) {
                        if (data.msgs[i].content.indexOf(this.EmojiData[j].expression_number) > -1) {
                          var emojiexpression = this.EmojiData[j].qiniuImg;
                          this.comments.unshift({
                          name: data.msgs[i].srcUser.alias,
                          type:data.msgs[i].srcUser.UserType,
                          avatar: data.msgs[i].srcUser.head, // || "/images/default/userDefault.png",
                          content: data.msgs[i].content,
                          time: this.formatDate(new Date(data.msgs[i].msgTime * 1000)),
                          emojitype: emojitype  ,
                          emojiexpression: emojiexpression
                        });
                        }
                      }
               }else{
                 this.comments.unshift({
                    name: data.msgs[i].srcUser.alias,
                    avatar: data.msgs[i].srcUser.head, // || "/images/default/userDefault.png",
                    content: data.msgs[i].content,
                    type:data.msgs[i].srcUser.UserType,
                    time: this.formatDate(new Date(data.msgs[i].msgTime * 1000)),
                    emojitype: 0 ,
                    emojiexpression: 0
                  });
               }


            }
            console.log(this.comments)
            if (this.commentbottom == true) {
              setTimeout(() => {//滚动到底部
                document.getElementsByClassName(
                  "comment"
                )[0].scrollTop = document.getElementsByClassName(
                  "txt"
                )[0].offsetTop;
              }, 500);
            }
          } else {
            this.commentLoading = false;
          }
          break;
      }
    },
    /**
     链接ws
      * */
    connect(host,port) {
      socketClient.connect(
        this.userId,
        "",
        this.courseId,
        this.onmessage,
        () => {

        },host,port
      );
    },
    /**
     * 获取banner跟直播间信息
     */
    getData() {
      this.$request.get("/BannerAndLive/fetchBannerAndLiveData", "", res => {
        console.log(res);
        if (res.code == 200) {
          this.userId = this.getCookie('username');
          this.connect(res.data.webSocket.host,res.data.webSocket.port);
          this.pull_url = res.data.pull_url;
          //封面
          this.live_cover = res.data.live_cover;
          this.push_status = res.data.push_status;
          //课程表
          this.courseBg = res.data.course_table;
          //免责声明
          this.disclaimer = res.data.disclaimer;
          this.getPullVideo();
        }
      });
    },
    /**
     * 播放
     */
    getPullVideo() {

      var pull_url;
      if (this.push_status == 1) {
        pull_url = this.pull_url;
      } else {
        pull_url = "///";
      }
      var videoObject = {
        container: "#videoplayer", //“#”代表容器的ID，“.”或“”代表容器的class
        variable: "player", //该属性必需设置，值等于下面的new chplayer()的对象
        autoplay: true, //自动播放
        live: true,
        //loaded: 'loadedHandler', //当播放器加载后执行的函数
        loop: false, //是否需要循环播放
        poster: this.live_cover,
        video: pull_url //"http://live.hkstv.hk.lxdns.com/live/hks/playlist.m3u8" //视频地址
      };
      setTimeout(() => {
        new ckplayer(videoObject);
      }, 1000);
    }
  },

  watch: {
    commentText(val) {
      if (val.trim() != "") {
        document.getElementById("sendBtn").className = "active";
      } else {
        document.getElementById("sendBtn").className = "";
      }
    }
  }
};
</script>

<style lang="scss">
$borderc: #eee;
main.index {
  height: 100%;
  background: #f6f6f6;
  .article {
    display: flex;
    height: calc(100% - 60px);
    margin-bottom: 10px;
    position: relative;
    margin-right: 12px;
    overflow: hidden;
  }
  &>header {
    height: 50px;
    line-height: 50px;
    text-align: left;
    background: #fff;
    border-bottom: 1px solid $borderc;
    .left{
      display: inline-block;
      .logo {
        width: 150px;
        text-align: left;
        height: 28px;
        margin: 0 34px;
      }
      .teacher{
        display: inline-block;
        margin-left: 12px;
        .head{
          width: 40px;
          height: 40px;
          border-radius: 50%;
          vertical-align: middle;
          margin-top: -5px;
        }
        span{
          font-size: 18px;
          color: #353535;
          display: inline-block;
          margin: 0 8px;
          font-weight: bold;
        }
        .live-logo{
          width: 14px;
          height: 14px;
          vertical-align: middle;
          margin-top: -6px;
        }
      }
    }
    .right{
      width: 450px;
      float: right;
      height: 50px;
      padding-right: 24px;
      .no-login{
        img{
          width: 30px;
          height: 30px;
          vertical-align: middle;
          margin-right: 4px;
          margin-top: -4px;
          border-radius: 50%;
        }
        span{
          font-size: 16px;
          color: #353535;
          font-weight: bold;
          cursor: pointer;
        }
        i{
          font-size: 16px;
          color: #ff5e2d;
          font-style: normal;
          float: right;
          cursor: pointer;
        }
      }
    }
  }
  .online-num {
    width: 220px;
    height:100%;
  }
  .comment-box {
    border: 1px solid $borderc;
    width: 438px;
    height: calc(100% - 12px);
    padding-bottom: 56px;
    background: #fff;
    margin-left: 12px;
    overflow: hidden;
    position: relative;
    box-sizing: border-box;
    margin-top: 12px;
    .comment-err{
      position: absolute;
      bottom: 20%;
      width: 60%;
      left: 20%;
      height: 35px;
      line-height: 35px;
      background: #eee;
      text-align: center;
      color: red;
      border-radius: 20px;
    }
    .his{
      position: absolute;
      top: 40px;
      width: 60%;
      left: 20%;
      height: 35px;
      line-height: 35px;
      background: #1380e6;
      text-align: center;
      color: #fff;
      border-radius: 20px;
    }
    .comment {
      overflow-y: scroll;
      position: relative;
      width: 100%;
      padding: 0 16px;
      height: calc(100% - 56px);
    }
    /*滚动条样式*/
    #sidebar::-webkit-scrollbar {
      width: 6px;
      height: 8px;
      background-color: #f5f5f5;
    }
    /*定义滚动条的轨道，内阴影及圆角*/
    #sidebar::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
      border-radius: 3px;
      background-color: #eee;
    }
    /*定义滑块，内阴影及圆角*/
    #sidebar::-webkit-scrollbar-thumb {
      height: 10px;
      border-radius: 3px;
      -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
      background-color: #c4c4c4;
    }

    .title {
      height: 50px;
      line-height: 50px;
      font-size: 18px;
      padding-left: 16px;
      color: #ff5e2d;
      border-bottom: 1px solid $borderc;
    }
    .footer {
      height: 56px;
      width: 100%;
      position: absolute;
      bottom: 0px;
      cursor: pointer;
      right: 0;
      padding: 8px 0;
      border-top: 1px solid $borderc;

      display: flex;
      justify-content: space-between;
      align-content: center;
      background-color: #fff;
      z-index: 11;
      input {
        border: 1px solid $borderc;
        width: 100%;
        margin-left: 8px;
        padding-left: 12px;
        padding-right: 44px;
        font-size: 14px;
        color: #888;
        border-radius: 20px;
      }
      .mask-login{
        position: absolute;
        width: 100%;
        bottom: 0;
        align-items: center;
        padding:0 18px 0 24px;
        height: 56px;
        display: flex;
        justify-content: space-between;
        background: #ff5e2d;
        p{
          font-size:18px;
          color:#fff;
        }
        .weixin{
          display:block;
          width: 138px;
          height: 34px;
          background:url('../assets/images/1.1/wechat_button_normal.png') center center no-repeat;
          background-size:cover;
          &:hover{
              background:url('../assets/images/1.1/wechat_button_pass.png') center center no-repeat;
              background-size:cover;
          }
        }
      }
      .emojiBtn{
        display: block;
        position: absolute;
        width: 30px;
        height: 30px;
        top: 13px;
        right: 62px;
        background: url("../assets/images/1.1/face_button.png") center center no-repeat;
        background-size: cover;
        &.active {
          background: url("../assets/images/1.1/face_close.png") center center
            no-repeat;
          background-size: cover;
        }
      }
      #sendBtn {
        width: 40px;
        height: 40px;
        flex-shrink: 0;
        display: inline-block;
        background: url("../assets/images/send_button_normal.png") center center
          no-repeat;
        background-size: 40px auto;
        margin: 0 8px;
        &.active {
          background: url("../assets/images/send_button_pass.png") center center
            no-repeat;
          background-size: 40px auto;
        }
      }
    }
  }
  .video-play {
    width: 1250px;
    overflow-y: auto;
    flex: 1;
    min-width: 750px;
    margin-left: 12px;
    margin-top: 12px;
    &>section{
      margin-bottom: 16px;
    }
  }
  /*滚动条样式*/
  .video-play::-webkit-scrollbar {
    width: 6px;
    height: 8px;
    background-color: #f5f5f5;
  }
  /*定义滚动条的轨道，内阴影及圆角*/
  .video-play::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    border-radius: 3px;
    background-color: #eee;
  }
  /*定义滑块，内阴影及圆角*/
  .video-play::-webkit-scrollbar-thumb {
    height: 10px;
    border-radius: 3px;
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #c4c4c4;
  }

  // 评论区
  .comment-item {
    display: flex;
    margin-top: 16px;

    h5 {
      height: 40px;
      line-height: 40px;
    }
    .name {
      color: #888;
      margin-right: 5px;
    }
    .time {
      color: #b2b2b2;
    }
    .type{
      display: inline-block;
      width:28px;
      height: 16px;
      vertical-align: middle;
    margin-right: 8px;
    }
    .ass{
      background: url('../assets/images/1.1/assistant_label_mini.png') center center no-repeat;
      background-size: cover;
    }
    .tea{
      background: url('../assets/images/1.1/teacher_label_mini.png') center center no-repeat;
      background-size: cover;
    }
    &>img {
      width: 40px;
      margin-right: 8px;
      height: 40px;
      border-radius: 50%;
    }
    p {
      padding: 12px;
      background: #f6f6f6;
      border-radius: 20px;
      color: #353535;
      img{
        width: 70px;
        height: 54px;
      }
    }
  }
}
</style>




